.about__container {
    display: grid;
  }
  
  .about__cards {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
  .mongoose{
    width:3rem;
    /* height:1rem; */
    margin-left: 42%;
  }
  .about__card {
    background: var(--color-bg-variant);
    border: .3px solid;
    border-radius: 1rem;
    padding: .8rem;
    text-align: center;
    justify-content: center;
  }
  .about__icon {
    color: var(--color-primary);
    font-size: 1.4rem;
  }
  
  .about__card h5 {
    font-size: .85rem;
  }
  
  .about__card small {
    font-size: 0.7rem;
    color: var(--color-light);
  }
  
  @media screen and (max-width: 1024px) {
    .about__container {
      grid-template-columns: 1fr;
    }
    .about__me {
      width: 50%;
      margin: 2rem auto 4rem;
    }
  
    .about__content p {
      margin:  1rem 0 1.5rem;
    }
  }


  @media screen and (max-width: 600px) {
    .about__me {
      width: 65%;
      margin: 0 auto 3rem;
    }
    .about__cards {
      grid-template-columns: 1fr 1fr;
      gap: 1rem;
    }
    .about__content {
      text-align: center;
    }
    .about__content p {
      margin: 1.5rem 0;
    }
  }