header{
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
 
}
.header__container {
  text-align: center;
  height: 100%;
  /* position: relative; */
}

.cta {
  margin-top: 2.5rem;
  display: flex; 
  gap: 1.2rem;
   justify-content: center;
}

.header__socials {
  margin-top: 1.5rem;
  display: flex;
  gap: 2rem;
  justify-content: center;
}

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left:  calc(20% - 11rem);
  bottom: 3rem;
}

.me {
  background: var(--color-primary);
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 25rem;
  /* position: absolute; */
  /* left: calc(50% - 11rem); */
  display: flex;
  margin-top: 1rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 0rem 0.5rem 0.5rem 0.5rem;
  margin-left: calc(50% - 11rem);
}

.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
  position: absolute;
  right:  calc(20% - 11rem);
}

@media screen and (max-width: 1024px) {
  /* .container {
    background-color: brown;
  } */
  .header {
    height: 68vh;
  }

}


@media screen and (max-width: 600px) {
  /* .container {
    background-color: pink;
  } */
  .header {
    height: 150vh;
  }

  .header__socials, .scroll__down {
    display: none;
  }
}