footer {
background: var(--color-primary);
/* padding: 2.5rem 0; */
text-align: center;
font-size: 0.9rem;
display:flex;
justify-content: space-between;
height: 100px;
    margin-top: 0px;
    padding-top: 27px;
position: sticky;
top: 0 ;
bottom: 0;
overflow: hidden;
}

footer a {
  color: var(--color-bg);
  margin-left: 2%;
  margin-top: -1%;
  width: 20%;
}

.footer__logo {
  font-size: 2rem;
  font-weight: 500;
  /* margin-bottom: 2rem; */
  display:inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}

.footer__socials {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
  margin-right: 11%;
}

.footer__socials a {
  background-color: var(--color-bg);
  color: var(--color-white);
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
  width: 39px;
    height: 34px;
    padding: 10px;
 
}
#navrr{
  width: max-content;
    align-items: center;
}
.footer__socials a:hover {
  background: transparent;
  color: var(--color-bg);
  border-color: var(--color-bg);
}

.footer__copyright {
  margin-bottom: 4rem;
  color: var(--color-bg);
}
@media screen and (max-width: 600px) {
  /* footer {
    display: grid;
  } */
  footer a {
    color: var(--color-bg);
    margin: auto;
  }
  #navrr{
    width: max-content;
    align-items: center;
  }
  .permalinks{
    display: none;
  }
  .footer__logo {
    margin-bottom: 2rem;
  }
  .footer__socials {
    margin-bottom: 1rem;
  }
}